.slider {
  position: relative;
  width: 1170px;
  margin: 0 auto;
  box-sizing: border-box; }

.slider__switch {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(253, 144, 79, 0.3);
  border: none;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer; }
  .slider__switch:hover {
    background-color: rgba(253, 144, 79, 0.8); }

.slider__switch--prev {
  left: 1%; }
  .slider__switch--prev:before {
    content: "";
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: white;
    top: 42%;
    left: 33%;
    transform: rotate(-35deg); }
  .slider__switch--prev:after {
    content: "";
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: white;
    top: 55%;
    left: 33%;
    transform: rotate(35deg); }

.slider__switch--next {
  right: 1%; }
  .slider__switch--next:before {
    content: "";
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: white;
    top: 42%;
    left: 33%;
    transform: rotate(35deg); }
  .slider__switch--next:after {
    content: "";
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: white;
    top: 55%;
    left: 33%;
    transform: rotate(-35deg); }

.slider__list {
  margin: 0;
