.slider__item--active {
  display: block; }

.slider__item--animators a {
  position: absolute;
  bottom: 10%;
  left: 30%;
  background-color: white;
  color: #4a256f; }
  .slider__item--animators a:hover {
    background-color: #ff5cd5;
    color: white; }

.slider__item--logopedist a {
  position: absolute;
  bottom: 10%;
  left: 23%;
  background-color: #ff607c;
  color: white; }
  .slider__item--logopedist a:hover {
    background-color: #3d8d1e;
