body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  width: 100%;
  font-family: "RobotoCondensedRegular", "Arial", sans-serif;
  font-size: 16px;
  background-color: #ffffff;
  background-image: url("../img/background2.png");
}

html,
body {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  @include visually-hidden;
}
